* {
    margin: 0;
    font-family: "Roboto", sans-serif;
}

html {
    box-sizing: border-box;
}

.container {
    max-width: 1180px;
    margin: 0 auto;
    display: flex;
}

.sidebar__friendlist-item--name,
.rightbar-friend__name,
.sidebar__list-item--text,
.rightbar__birthday-text b,
.post__top-name {
    border-bottom: 1px solid #fff;
}

.sidebar__friendlist-item:hover .sidebar__friendlist-item--name,
.rightbar-friend__item:hover .rightbar-friend__name,
.sidebar__list-item:hover .sidebar__list-item--text,
.rightbar__birthday-text b:hover,
.post__top-left:hover .post__top-name {
    border-bottom: 1px solid #ccc;
    cursor: pointer;
}

.text_name {
    font-size: 16px;
}

@media (max-width: 1100px) {
    .rightbar__ad {
        margin: 15px 0;
    }

    .sidebar__btn {
        width: 100px;
    }

    .text_name {
        font-size: 14px;
        font-weight: 500;
    }

    .rightbar {
        width: 25%;
    }

    .rightbar__wrapper {
        padding: 10px 10px 0 0;
    }

    .rightbar__followings {
        gap: 10px;
    }

}

@media (max-width: 900px) {
    .text_name {
        font-size: 12px;
    }

    .sidebar__friendlist-item,
    .rightbar-friend__item {
        column-gap: 5px;
        margin-bottom: 5px;
    }

    .rightbar-friend__img-box {
        margin-right: 0;
    }

    .share__bottom {
        gap: 20px;
    }

    .share__bottom-options {
        column-gap: 5px;
    }

    .text {
        font-size: 14px;
    }

    .post__center {
        margin: 10px 0;
    }

    .post__center-img {
        margin-top: 10px;
    }

    .post {
        margin: 10px 0;
    }

    .rightbar__birthday-img {
        margin-left: 0;
    }

    .feed {
        padding: 10px;
    }

    .sidebar__list-item {
        margin-bottom: 5px;
        column-gap: 5px;
    }

    .rightbar__following-img {
        width: 65px;
        height: 65px;
    }
}

@media (max-width: 800px) {
    .text {
        font-size: 12px;
    }

    .rightbar__title {
        font-size: 14px;
    }
}

@media (max-width: 750px) {
    .profile .rightbar {
        width: 30%;
        height: auto;
        position: relative;
        border-radius: 10px;
        box-shadow: 0px 0px 12px -6px rgb(0, 0, 0);
        top: 10px;
        transition: 0.5s;
        z-index: 666;
        background-color: #fff;
    }

    .profile .rightbar {
        right: -177px;
        padding-left: 10px;

    }

    .profile .rightbar.active {
        right: 0;
    }

    .profile .feed {
        width: 100%;
        margin-right: -185px;
    }

    .profile .sidebar {
        height: auto;
    }

    .profile-right__img-cover {
        height: 200px;
    }

    .profile-right__img-user {
        top: 115px;
    }

    .profile-right__top-cover {
        height: 275px;
    }
}

@media (max-width: 680px) {

    .sidebar,
    .rightbar {
        width: 30%;
        height: auto;
        position: relative;
        border-radius: 10px;
        box-shadow: 0px 0px 12px -6px rgb(0, 0, 0);
        top: 10px;
        transition: 0.5s;
        z-index: 666;
        background-color: #fff;
    }

    .sidebar {
        overflow-y: visible !important;
        left: -233px;
    }

    .rightbar {
        right: -200px;
        padding-left: 10px;

    }

    .rightbar.active {
        right: 0;
    }

    .sidebar.active {
        left: 0;
    }

    .feed {
        width: 100%;
        margin-left: -230px;
        margin-right: -200px;
    }

    .share__bottom-options {
        column-gap: 20px;
    }

    .toggle-button__sitebar,
    .toggle-button__rightbar {
        position: absolute;
        display: block;
    }

    .profile-right {
        width: 100%;
        margin-left: -241px;
        height: 190px;
    }

    .profile-right__img-user {
        top: 100px;
    }

    .profile .sidebar {
        top: 190px;
    }

    .profile-right__img-cover {
        height: 185px;
    }

    .profile-right__top-cover {

        height: 265px;
    }
    .profile .rightbar{
        right: -192px;
        top: -120px;
    }
    .profile .feed{
         margin-left: 10px;
    }
      
}

@media (max-width: 640px) {
    .sidebar {
        left: -224px;
    }

    .rightbar {
        right: -190px;
    }

    .feed {
        margin-right: -190px;
    }
}

@media (max-width: 600px) {
    .sidebar {
        left: -214px;
    }

    .rightbar {
        right: -182px;
        width: 40%;
    }

    .feed {
        margin-left: -210px;
        margin-right: -187px;
    }
}

@media (max-width: 570px) {
    .sidebar {
        left: -202px;
    }

    .rightbar__wrapper {
        margin: 0;
        padding: 10px 10px 0 0;
    }
    .profile .rightbar{
        right: -175px;
    }
    .profile .feed {
        width: 95%;
        margin-left: 30px;
}

}

@media (max-width: 520px) {
    .sidebar {
        left: -190px;
    }

    .feed {
        margin-left: -190px;
        margin-right: -200px;

    }

    .rightbar {
        right: -200px;
    }
    .profile .feed{
        margin-left: 44px;
    }
}

@media (max-width: 490px) {
    .sidebar {
        left: -180px;
    }
}

@media (max-width: 470px) {
    .share__bottom-options {
        column-gap: 10px;
    }

    .share__bottom {
        flex-wrap: wrap;
        row-gap: 10px;
    }

    .share {
        height: 200px;
    }
}

@media (max-width: 450px) {
    .sidebar {
        left: -164px;
    }

    .feed {
        margin-left: -170px;
        margin-right: -200px;
    }

    .rightbar {
        right: -195px;
        width: 50%;
    }
    .profile .feed{
        margin-left: 77px;
        width: 80%;
    }
    .profile .rightbar {
        right: -182px;
}
.profile .rightbar.active {
    right: -27px;
}
.profile-right__img-user {
    left: 70px;
}
}

@media (max-width: 410px) {
    .sidebar {
        left: -154px;
    }

    .feed {
        margin-left: -155px;
    }

}

@media (max-width: 390px) {
    .share__bottom-options {
        flex-wrap: wrap;
        justify-content: space-around;
        row-gap: 10px;
    }

    .share__btm {
        margin: 0 auto;
    }

    .share {
        height: 230px;
    }
}

@media (max-width: 370px) {
    .sidebar {
        width: 50%;
        left: -210px;
    }

    .feed {
        margin-left: -218px;
        margin-right: -190px;
    }

    .rightbar {
        right: -185px;
    }
    .profile .feed {
        margin-left: 30px;
        width: 88%;
}
.profile .rightbar.active{
    right: 0;
}
.profile .rightbar{
    width: 45%;
}
.profile-right__img-user {
    left: 35px;
}
}

@media (max-width: 320px) {
    .sidebar {
        width: 57%;
    }

    .feed {
        margin-right: -185px;
    }

    .rightbar {
        right: -180px;
        width: 57%;
    }
    .profile .feed{
        width: 85%;
    }
    .profile .rightbar {
        width: 52%;
        right: -180px;
}
}